import { useRef, useState, useEffect } from 'react';
import {
  NO_INPUT_ERROR,
  NO_MASK_ERROR,
  createTextMaskInputElement,
  isArray,
  isNil
} from '../index';

/**
 * Custom hook to give an input element Text-Masking capabilities
 * This component will default w/ the empty string as the `value setting
 * @param {Object} props
 * @return {Object} shape is as follows...
 * {
 *    getInputProps: {
 *      onChange: Function,
 *      ref: Object,
 *      value: String -- Defaults to empty string. This may set any `controlled` boolean flags to TRUE (e.g. props.value !== undefined)
 *    },
 *    meta: {
 *      adjustedCaretPosition: (undefined|Number),
 *      placeholder: String
 *    }
 * }
 */
export default function useTextMask(props = {}) {
  const {
    guide = false,
    mask,
    onChange,
    placeholderChar = 'X',
    upperCaseValue = false,
    value
  } = props;

  const inputRef = useRef(null);
  const textMask = useRef(null);
  const [state, setState] = useState({
    adjustedCaretPosition: undefined,
    conformedValue: isNil(value) ? '' : value.toString(),
    placeholder: undefined
  });

  const createTextMask = () =>
    createTextMaskInputElement({
      guide,
      inputElement: inputRef.current,
      mask,
      placeholderChar,
      upperCaseValue
    });

  useEffect(() => {
    textMask.current = createTextMask();
    const {
      adjustedCaretPosition,
      conformedValue,
      previousPlaceholder: placeholder
    } = textMask.current.update();
    setState({ adjustedCaretPosition, conformedValue, placeholder });
  }, [guide, mask, placeholderChar]);

  useEffect(() => {
    if (textMask.current) {
      const {
        adjustedCaretPosition,
        conformedValue,
        previousPlaceholder: placeholder
      } = textMask.current.update(value);
      setState({ adjustedCaretPosition, conformedValue, placeholder });
    }
  }, [value]);

  useEffect(() => {
    if (inputRef.current) {
      if (isArray(mask) && mask.length > 0) {
        textMask.current = createTextMask();
        const {
          adjustedCaretPosition,
          conformedValue,
          previousPlaceholder: placeholder
        } = textMask.current.update(value);
        setState({ adjustedCaretPosition, conformedValue, placeholder });
      } else {
        throw new Error(NO_MASK_ERROR);
      }
    } else {
      throw new Error(NO_INPUT_ERROR);
    }
  }, []);

  /**
   * @param {Event} e
   * @protected
   */
  function handleChange(e) {
    const {
      adjustedCaretPosition,
      conformedValue,
      previousPlaceholder: placeholder
    } = textMask.current.update();

    const newState = {
      adjustedCaretPosition,
      conformedValue,
      placeholder
    };

    setState(newState);

    typeof onChange === 'function' && onChange(e, newState);
  }

  return {
    getInputProps: {
      onChange: handleChange,
      ref: inputRef,
      value: state.conformedValue
    },
    meta: {
      adjustedCaretPosition: state.adjustedCaretPosition,
      placeholder: state.placeholder
    }
  };
}

/**
 * Render Prop implemention using our custom hook
 * @constructor
 */
export function TextMaskInput({ children, ...props }) {
  return children(useTextMask(props));
}
